*{
  font-family: initial;
  font-size: 18px;
}
body{
  display: flex;
  justify-content: center;
  align-items: center;
}
.contaire{
  
  left: 50%;
  position: absolute;
  transform: translate(-50%,50%);
  margin: 0 auto;
  width: 256px;
  text-align: center;
  border: 10px solid #101116;
  background: #101116;
  border-radius: 20px;
}
#keypade{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-auto-rows: minmax(60px,auto);
}
input{
  height: 75px;
  width: 249px;
  background-color:beige;
  border: 0px;
  color:black;
  text-align: right;
  font-size: 25px;
  font-weight: 500;
  letter-spacing: 1px;
  border-radius: 12px;

}
#Clear{
  grid-column: 1/3;
  grid-row: 1;
  color: #262834;
}
#reslt{
  grid-column: 3/5;
  grid-row: 5;
  color:beige;
 
}
#backspace{
  color: #262834; 
}
button{
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background: #262834;
  color: #ffffff;
  font-weight: 500;
}
input:focus,
input:active,
input:focus,
input:active{
  outline: none;
}
.highlight{
  background: rgb(252, 217, 15);
}

